import { Component } from '@angular/core';
import { ConversationMessagesService } from '../services/conversation-messages.service';

@Component({
  selector: 'app-tab2',
  templateUrl: 'tab2.page.html',
  styleUrls: ['tab2.page.scss'],
  standalone: false,
})
export class Tab2Page {

  file!: File; // Variable to store file
  loading: boolean = false; // Flag variable
  constructor( private conversationMessagesService: ConversationMessagesService,) {}

  onFileChange(fileChangeEvent:any) {
    this.file = fileChangeEvent.target.files[0];
  }

  upload()
  {
    this.loading = true;
     // Create form data
     const formData = new FormData();

     // Store form name as "file" with file data
     formData.append("file", this.file, this.file.name);

    console.log(formData,this.file);
    this.conversationMessagesService.uploadFileIndex(formData).subscribe(
      (res) => {
        this.loading = false;
      },
      (err) => {
        this.loading = false;
        console.log("caught error {}", err);
      }
    )
  }


}
